.screen {
  .intro__message{
    opacity: 0;
    animation: textFadeIn 0.5s ease-out 0.5s 1 backwards;
    animation-play-state: paused;
  }

  .swiper-slide-active,
  .swiper-slide-next{
    .slider__item-text {
      opacity: 0;
      animation: textFadeIn 0.5s ease-in-out 0.5s 1 backwards;
      animation-play-state: paused;
    }
  }

  .form__field {
    transform: scaleX(0);
    transform-origin: 100% 50%;
    transition: transform 0.5s ease 0.2s;
  }

  &.active{
    .intro__message {
      opacity: 1;
      animation-play-state: running;
    }

    .swiper-slide-active {
      .slider__item-text{
        opacity: 1;
        animation-play-state: running;
      }
    }

    @media (min-width: $mobile) {
      .swiper-slide-next {
        .slider__item-text{
          opacity: 1;
          animation-play-state: running;
        }
      }
    }

    .form__field {
      transform: scaleX(1);
    }
  }
}

// rules
.rules__item{
  &::before{
    transform: scale(0);
    animation: rulesIncrementCircle 0.3s $fade-ease 1s 1 backwards;

    .screen.active & {
      transform: scale(1);
    }
  }

  p {
    animation: textFadeIn .5s $fade-ease 1s 1 backwards;
    animation-play-state: paused;

    .screen.active & {
      animation-play-state: running;
    }
  }
}

.rules__link.btn {
  &::before{
    width: 60px;
    right: 0;
    left: unset;
    transform: translate(0,-50%);
    opacity: 0;
    transition: width .4s ease-out 2s, opacity .4s ease 1.8s;
  }

  span{
    opacity: 0;
    transition: opacity .4s ease 2.35s;
  }

  .screen.active & {
    &::before{
      width: 100%;
      right: 0;
      opacity: 1;
    }

    span{
      opacity: 1;
    }
  }
}

@for $i from 0 through 10 {
  $delay: $i * 0.25s + 0.2s;
  .rules__item:nth-of-type(#{$i})::before {
    animation-delay: $delay;
  }
}

@for $i from 0 through 10 {
  $delay: $i * 0.3s + 0.3s;
  .rules__item:nth-of-type(#{$i}) p {
    animation-delay: $delay;
  }
}

// slider
.slider__control {
  transition: transform 1s ease .3s, opacity .5s ease;
  svg,
  path{
    transition: color .3s ease;
  }
  &--prev {
    transform: translateX(-50px) rotate(-90deg);
    opacity: 0;
    .screen.active & {
      transform: translateX(0) rotate(0);
      opacity: 1;
      &.swiper-button-disabled {
        opacity: 0.5;
      }
    }
  }

  &--next {
    transform: translateX(50px) rotate(90deg);
    opacity: 0;
    .screen.active & {
      transform: translateX(0) rotate(0);
      opacity: 1;
      &.swiper-button-disabled {
        opacity: 0.5;
      }
    }
  }
}
.slider__pagination {
  opacity: 0;
  transition: opacity 1s ease .5s;
  .screen.active & {
    opacity: 1;
  }
}
